    props: {
      goodsItem: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    methods: {
      /** :
       * ,
       * , scroll
       * scroll
       *    js: img.onload = function() {}
       *    Vue: @load=''
       * GoodsItem.vue, scrollHome.vue
       * ,
       * : Vuex
       * :  ()
       * : 1.Vue, :
       *          Vue.prototype.$bus = new Vue()
       *          Vue
       *      2.:
       *          this.$bus.$emit('', )
       *      3.:
       *          this.$bus.$on('', ())
       *
       * */
      imageLoad() {
        this.$bus.$emit('itemImageLoad')
      }
    }
  }
</script>

