<template>
	<div class="wrapper" ref="wrapper">
    <div class="content"><slot></slot></div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    name: "Scroll",
    props:{
      probeType: {
        type: Number,
        default: 0,
      },
      pullUpLoad: {
        type: Boolean,
        default: false,
      }
    },
    data() {
      return {
        scroll: null,
      }
    },
    mounted() {
      /** 1.BScroll */
      // 1.1
      // this.scroll = new BScroll('.wrapper', {})
      // 1.2
      // this.scroll = new BScroll(document.querySelector('.wrapper'), {})
      // 2, this.$refs, DOM
      this.scroll = new BScroll(this.$refs.wrapper, {
        click: true, // , vueclick
        // clicktrue, button
        // clicktrue, div
        probeType: this.probeType, // , 0()1; 2,; 3, 
        pullUpLoad: this.pullUpLoad, // BScroll
      })
      /** 2. */
      if (this.probeType === 2 || this.probeType===3) {
        this.scroll.on('scroll', (p) => {
          // console.log(p);
          this.$emit('scroll', p)
        })
      }
      /** 3.scroll */
      if (this.probeType === 3) {
        this.scroll.on('pullingUp', () => {
          this.$emit('pullingUp')
        })
      }
    },
    methods: {
      scrollTo(x, y, time=300) {
        this.scroll.scrollTo(x, y, time)
      },
      finishPullUp() {
        this.scroll.finishPullUp()
      },
      refresh() {
        /**
         * this.scroll, this.scroll
         * ?
         * ,  refresh()
         * , , scroll,
         * scrollundefined, 
         * 
         *
         * */
        console.log('refresh---');
        this.scroll && this.scroll.scrollTo && this.scroll.refresh()
      }
    }
  }
</script>

